/* Variables */
@media screen and (max-width: 480px) {
	div.ImageContainer {
		width: 80vw;
		height: 50vh;
	}
}

@media screen and (min-width: 480px) and (max-width: 992px) {
	div.ImageContainer {
		width: 40vw;
		height: 60vh;
	}
}

:root {
	--glitch-width: 100vw;
	--glitch-height: 100vh;

	--gap-vertical: 5px;
	--gap-horizontal: 10px;

	--blend-color-1: transparent;
	--blend-color-2: transparent;
	--blend-color-3: #a4161a;
	--blend-color-4: blue;
	--blend-color-5: #14213d;

	--blend-mode-1: none;
	--blend-mode-2: difference;
	--blend-mode-3: overlay;
	--blend-mode-4: hard-light;
	--blend-mode-5: exclusion;

	--time-anim: 4s;
	--delay-anim: 2s;
}

.ImageContainer {
	position: relative;
	width: 100vw;
	height: 100vh;
	max-width: 400px;
	max-height: calc(400px * 1.25);
	overflow: hidden;
	margin: 0 auto;
	border-radius: 5px;
	box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2), 0 4px 4px rgba(0, 0, 0, 0.2),
		0 8px 8px rgba(0, 0, 0, 0.2), 0 16px 16px rgba(0, 0, 0, 0.2),
		0 32px 32px rgba(0, 0, 0, 0.2);
	transition: 0.2s all;
}

.ImageContainer:hover {
	cursor: crosshair;
}

.ImageGlitch {
	position: absolute;
	top: calc(-1 * var(--gap-vertical));
	left: calc(-1 * var(--gap-horizontal));
	width: calc(100% + var(--gap-horizontal) * 2);
	height: calc(100% + var(--gap-vertical) * 2);
	background: url('../../../../Assets/Images/Me/profile.jpg') no-repeat 50% 0;
	background-color: yellow;
	background-size: cover;
	background-blend-mode: var(--blend-mode-1);
}

.ImageGlitch:nth-child(2) {
	background-color: var(--blend-color-1);
	background-blend-mode: var(--blend-mode-2);
}

.ImageGlitch:nth-child(3) {
  background-color: var(--blend-color-1);
	background-blend-mode: var(--blend-mode-3);
}

.ImageGlitch:nth-child(4) {
	background-color: var(--blend-color-5);
	background-blend-mode: var(--blend-mode-4);
}

.ImageGlitch:nth-child(5) {
	background-color: var(--blend-color-3);
	background-blend-mode: var(--blend-mode-2);
}

/* all layers except the first one are hidden by default*/
.ImageGlitch:nth-child(n+2) {
	opacity: 0;
}

/* on hover we show all layers */
.ImageContainer:hover .ImageGlitch:nth-child(n+2) {
	opacity: 1;
}

/* apply the animations and a transform to each layer */
.ImageContainer:hover > .ImageGlitch:nth-child(2) {
	transform: translate3d(var(--gap-horizontal),0,0);
	animation: glitch-anim-1-horizontal var(--time-anim) infinite linear alternate;
}

.ImageContainer:hover > .ImageGlitch:nth-child(3) {
	transform: translate3d(calc(-1 * var(--gap-horizontal)),0,0);
	animation: glitch-anim-2-horizontal var(--time-anim) infinite linear alternate;
} 

.ImageContainer:hover > .ImageGlitch:nth-child(4) {
	transform: translate3d(0, calc(-1 * var(--gap-vertical)), 0) scale3d(-1,-1,1);
	animation: glitch-anim-3-horizontal var(--time-anim) infinite linear alternate;
}

/* Hover flash animation on last image */
.ImageContainer:hover > .ImageGlitch:nth-child(5) {
	animation: glitch-anim-flash 0.5s steps(1,end) infinite;
}

@keyframes glitch-anim-1-horizontal {
