	--gap-vertical: 5px;
	--gap-horizontal: 10px;

	--blend-color-1: transparent;
	--blend-color-2: transparent;
	--blend-color-3: #a4161a;
	--blend-color-4: blue;
	--blend-color-5: #14213d;

	--blend-mode-1: none;
	--blend-mode-2: difference;
	--blend-mode-3: overlay;
	--blend-mode-4: hard-light;
	--blend-mode-5: exclusion;

	--time-anim: 4s;
	--delay-anim: 2s;
}

